.index {
  background-color: #fff;
  min-height: 100%;
  padding-bottom: 10px;
}
.grid-section {
  margin-bottom: 16px;
}
.img {
  width: 100%;
}
.map-container {
  width: 100%;
  background-color: #fff;
  position: relative;
  padding-bottom: 42%;
  height: auto;
}
.container {
  width: 100%;
  height: 100%;
}
.carousel-container {
  display: block;
  width: 100%;
  padding-bottom: 50%;
  position: relative;
}
.carousel-main {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
}
.carousel-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.carousel-link {
  display: block;
  width: 100%;
}
.carousel-img {
  max-width: 100%;
  max-height: 100%;
  vertical-align: top;
}
:global .carousel-body .slider-list {
  height: 100%!important;
}
